:root {
    --bg-color: #0c0c0c;
}

ion-icon {
    cursor: pointer;
}

.menu-box-content {
    font-family: "Gotham Pro Medium";
    display: flex;
    flex: 1 1 0%;
    /*background-color: rgb(12, 12, 12);*/
    flex-direction: column;
    outline: none;
    width: 473px;
    background: var(--form-background);
    border-radius: 5px;
    padding: 2rem;
    
}

.menu-box-footer {
    background: var(--form-background);
    border: 0.5px solid rgba(79, 79, 79, 0.5);
    border-radius: 5px;
    padding: 2rem;
    margin-top: 0.5rem;
}

.logo-info__text-bold {
    font-family: "Gotham Pro Medium";
    font-weight: bold;
}

.logo-info__text-normal {
    font-family: "Gotham Pro Medium";
}

.menu-box-content div, label {
    outline: none;
}

.menu-box {
    display: flex;
    position: absolute;
    min-height: 568px;
    right: 40px;
    top: 90px;
    flex-direction: column;
}

.checkbox-container {
    position: relative;
    cursor: pointer;
}

.checkbox-container input {
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
}

.checkmark {
    display: flex;
    justify-content: center;
    align-items: center;
    top: 0;
    left: 0;
    height: 16px;
    width: 16px;
    border: 0.5px solid #FFFFFF;
    border-radius: 4px;
}

.checkmark:hover {
    background-color: #ccc;
}

.checkbox-container input:checked ~ .checkmark {
    background-color: #fff;
}

.checkmark:after {
    content: "";
    display: none;
}

.checkbox-container input:checked ~ .checkmark:after {
    display: block;
}

.checkbox-container .checkmark:after {
    width: 4px;
    height: 8px;
    border: solid #000;
    border-width: 0 2px 2px 0;
    margin-bottom: 2px;
    margin-left: 1px;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
}

.menuContainer {
    outline: none;
}

.menu-item {
    -moz-user-select: none;
    -ms-user-select: none;
    -webkit-user-select: none;
    user-select: none;
    margin-bottom: 0.4rem;
    border-radius: 5px;
    padding: 3% 2% 3% 2%;
    background-color: rgb(56, 56, 56);
}

.menu-item:hover {
    background-color: var(--hover-background);
    color: white;
    transition: 0.2s ease-in-out;
}
