}
.menu-item-inverted {
    background-color: var(--active-background);
    border-radius: 5px;
    margin-bottom: 0.4rem;
    border: 0.5px solid transparent;
    padding: 3% 2% 3% 2%;
}

.list-arrow-icon {
    width: 24px;
    height: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
}

