.hmenu__gunshop__navpanel {
    display: flex;
    flex-direction: column;
    width: 28%;
    margin-right: 5px;
    background: var(--form-background);
    border: 0.5px solid rgba(46, 46, 46, 0.5);
    padding-left: 2rem;
    padding-right: 2rem;
    border-radius: 5px;
}
.hmenu__content__exitbtn__container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin-top: 15px;
}
.hmenu__gunshop__navpanel__exitbtn:hover {
    background-color: var(--rojo);
}
.hmenu__gunshop__navpanel__exitbtn {
    font-family: 'Gotham Pro Regular';
    font-size: 1rem;
    letter-spacing: 0.05rem;
    padding: 1rem 4rem 1rem 4rem;
    margin-top: 5%;
    margin-bottom: 10%;
    background: var(--gris-item);
    border: 0.5px solid rgba(255, 255, 255, 0.25);
    border-radius: 3px;
    cursor: pointer;
}

.hmenu__gunshop__navpanel__item {
    margin-left: 1.5rem !important;
    margin-right: 1.5rem !important;
}

.hmenu__gunshop__navpanel__list {
    display: flex;
    flex-direction: column;
    overflow-x: auto;
}

.hmenu__gunshop__navpanel__list__item {
    cursor: pointer;
    border: 0.5px solid rgba(120, 120, 120, 0.25);
    padding-top: 1rem;
    padding-bottom: 1rem;
    margin: 0.2rem 1rem 0.2rem 1rem;
    text-align: center;
    font-size: 1.3rem;
    border-radius: 4px;
    background-color: var(--gris-item);

}
.hmenu__gunshop__navpanel__list__item:hover {
    background-color: var(--gris-hover);
}
.hmenu__content__list__item__name {
    font-family: 'Gotham Pro Regular';
    font-size: 1.2rem;
    letter-spacing: 0.05rem;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}
.hmenu__gunshop__navpanel__list__item_active {
    cursor: pointer;
    border: 0.5px solid rgba(180, 180, 180, 0.25);
    padding-top: 1rem;
    padding-bottom: 1rem;
    margin: 0.2rem 1rem 0.2rem 1rem;
    text-align: center;
    font-size: 1.3rem;
    border-radius: 4px;
    background-color: var(--gris-hover);
}

.hmenu__gunshop__navpanel__banner {
    height: 15%;
    padding: 20%;
    width: auto;
}