:root {
    --form-background: rgb(45, 45, 45);
    --form-input-background: rgba(110, 110, 110, 0.35);
    --hover-background: rgb(200, 122, 32);
    --active-background: rgb(172, 120, 60);

    --verde: rgb(20, 130, 20);
    --rojo: rgb(166, 16, 16);
    --azul: rgb(37, 30, 141);
    --amarillo: rgba(251, 232, 27, 0.721);
    --naranja: rgb(229, 98, 33);

    --gris-item: rgba(56, 56, 56);
    --gris-hover: rgba(66, 66, 66); 
}

body {
    margin: 0 auto;
    color: #fff;
    user-select: none;
    cursor: url("https://i.pinimg.com/originals/72/ee/21/72ee21718fe7b5b8f7b6211fe6153bc7.png"), pointer;
}

li {
    list-style: none;
}

input[type=range] {
    -webkit-appearance: none;
    outline: none;
}

input[type="radio"] {
    display: none;
}

input[type="checkbox"] {
    display: none;
}

input[type="text"], input[type="password"] {
    background: none;
    border: none;
    outline: none;
}

button, a {
    padding: 0;
    border: none;
    font: inherit;
    color: inherit;
    background-color: transparent;
    outline: none;
    cursor: pointer;
    text-decoration: none;
}

input {
    cursor: pointer;
}

::-webkit-scrollbar {
    width: 0;
    height: 0;
}

input:focus::-webkit-input-placeholder {
    color: transparent;
}

.container {
    width: auto;
    height: auto;
}

.hide {
    display: none !important;
}
img { pointer-events: none; }
.box{
	width: -webkit-fill-available;
  height: -webkit-fill-available;
  display: flex;
  position: absolute;
}