}

.speedbox-score {
    position: absolute;
    left: 0;
    top: 0;
    transform: rotate(-45deg);
    width: 390px;
    height: 390px;
    background: transparent;
    border-radius: 50%;
    border: 14.69px solid #48b9f2;
    border-color: transparent transparent #48b9f2 #48b9f2;
    box-sizing: border-box;
    cursor: pointer;
    z-index: 1;
    transition: transform 0.3s ease;
    background-color: rgba(0, 0, 0, 0.5);
}

