.svg-speedometer {
    display: block;
    width: 100px;
    height: 100px;
    transform: scale(2.35) rotate(40deg);
    margin-left: 6px;
    margin-bottom: 5px;
  }
  
.path-speedometer {
    stroke-linecap: round;
    stroke-width: 4;
    fill: none;
  }
  
  path.speedometer__fullbar {
    stroke: rgba(255, 115, 0, 0.2);
    stroke-dasharray: calc(40 * 3.142 * 1.85);
  }
  
  path.speedometer__progress {
    stroke: rgb(255, 137, 64);
    stroke-dasharray: calc(40 * 3.142 * 1.85);
    /* adjust last number for variance */
  }

