.display {
    display: inline-block;
    width: 2.5em;
    height: 1.2em;
    font-size: 28pt;
    line-height: 45px;
    margin: 2px;
    font-family: 'DSEG7Classic';
    border: 3px solid #000;
}

.display::after {
    position: absolute;
    content: '888';
}

.display span {
    z-index: 100;
    position: absolute;
}

.display.orange span { color: #f79e00; }
.display.orange { color: #592c00; background-color: #3d1c00; }

.display.red span { color: #ff0000; }
.display.red { color: #350000; background-color: #230303; }

.display.green span { color: #18f400; }
.display.green { color: #0e3800; background-color: #072600; }

.display.plate {
    display: block;
    width: 6em;
    height: 1.2em;
    line-height: 1.1em;
    font-size: 16pt;
    border: 2px solid #000;
    font-family: Consolas;
    letter-spacing: 0.1em;
