/*checkbox*/
.u-checkbox-style {
    display: unset !important;
}

.checkbox {
    margin-right: 20px;
    position: relative;
    display: inline-block;
    line-height: 1;
    font-size: 0;
    vertical-align: middle;
}

.checkbox input[type="radio"] {
    position: absolute;
    top: 0;
    left: 0;
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    opacity: 0;
    cursor: pointer;
}

.checkbox input[type="radio"] ~ .checkbox-icon {
    position: relative;
    display: inline-block;
    width: 16px;
    height: 16px;
    border: 2px solid rgba(0, 0, 0, 0.54);
    border-radius: 5px;
}

.checkbox input[type="radio"]:hover ~ .checkbox-icon,
.checkbox input[type="radio"]:focus ~ .checkbox-icon {
    border-color: #999;
}

.checkbox input[type="radio"]:checked ~ .checkbox-icon {
    background: #fff;
    border-color: #000000;;
}

.checkbox input[type="radio"]:checked ~ .checkbox-icon:after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    width: 8px;
    height: 8px;
    margin: -5px 0 0 -5px;
    background: #000000;
    border: 1px solid #000000;
    border-radius: 50%;
}

.checkbox input[type="radio"]:disabled ~ .checkbox-icon {
    background-color: #eee;
    border-color: #ddd;
    cursor: default;
}

.checkbox input[type="radio"]:disabled ~ .checkbox-icon:after {
    border-color: #bbb;
}

.checkbox-2x input[type="radio"] ~ .checkbox-icon {
    width: 32px;
    height: 32px;
    border-radius: 10px;
    border-width: 2px;
}

.checkbox-2x input[type="radio"]:checked ~ .checkbox-icon:after {
    margin: -8px 0 0 -10px;
    width: 14px;
    height: 6px;
    border-width: 6px;
    border-radius: 4px;
}

.checkbox-circle input[type="radio"] ~ .checkbox-icon {
    border-radius: 50%;
}

