    font-size: 14px;
    color:#fff;
    margin-bottom: 10px;
    margin-top: 10px;
    margin-left: 6%;
}

.MuiInputBase-inputMultiline {
    font-family: 'SF Pro Regular' !important;
}

.MuiInput-input {
    font-family: 'SF Pro Regular' !important;
}

.position-btn-addchat{
    display: flex;
    width: 100%;
    justify-content: center;
    margin-top: auto;
    margin-bottom: 18%;
}

.ios__button {
    font-family: 'SF Pro Semibold';
    font-size: 1rem;
    background: rgba(118, 118, 128, 0.24);
    border-radius: 14px;
    width: 80%;
    display: flex;
    justify-content: center;
    padding-top: 1rem;
    padding-bottom: 1rem;
    padding-left: 6%;
    padding-right: 6%;
}

.position-btn-addchat .MuiButton-containedPrimary {
    background-color: #212d3b;
}
.position-btn-addchat .MuiButton-containedPrimary:hover {
    background-color: #2a3848;
}
.messenger-addchat::-webkit-scrollbar {
    display: none;
}
.spiceal-ts-chat {

}

.dedbit__messenger__chatname {
    margin-left: 30%;
}

.rotate-components .posit-icon-del-chat {
    margin-right: 2%;
}
.posit-icon-del-chat {
    margin-right: 5%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.loader-style-addchat {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.loader-style-addchat .MuiCircularProgress-colorPrimary {
    color: #213246;
}
.loader-style-addchat .MuiCircularProgress-root.MuiCircularProgress-colorPrimary.MuiCircularProgress-indeterminate {
    width: 60px !important;
    height: 60px !important;
}

.chevron {
    margin-right: 10%;
}

.umenu-title-family .chevron {
    color: #757575;
}

.chevron[data-hidden=true]::before {
    border-style: solid;
	border-width: 0.05em 0.05em 0 0;
	content: '';
	display: inline-block;
	height: 0.4em;
	position: relative;
	vertical-align: top;
    width: 0.4em;
    top: 0.26em;
    transform: rotate(135deg);
}

.chevron[data-hidden=false]::before {
    border-style: solid;
	border-width: 0.05em 0.05em 0 0;
	content: '';
	display: inline-block;
	height: 0.4em;
	position: relative;
	vertical-align: top;
    width: 0.4em;
    top: 0.26em;
    transform: rotate(225deg);
}

.umenu-title-name {
    margin-right: auto;
}