.inter-menu {
    background: var(--form-background);
    border: 0.5px solid rgba(80, 80, 80, 0.5);
    box-sizing: border-box;
    border-radius: 5px;
    width: 260px;
    position: absolute;
    z-index: 1;
    padding: 1rem;
    backdrop-filter: blur(4px);
}

.inter-box {
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: "Gotham Pro Regular";
    font-size: 16px;
    color: #fff;
    border: 0.5px solid rgba(255, 255, 255, 0.25);
    box-sizing: border-box;
    border-radius: 6px;
    margin-bottom: 0.7rem;
    padding: 0 0.25rem 0 0.25rem;
}

.inter-box:hover {
    background: var(--form-background);
    border: 0.5px solid rgba(80, 80, 80, 0.25);
    box-sizing: border-box;
    border-radius: 5px;
    color: #fff !important;
}