.accountmenu__content {
    height: 60%;
    border-radius: 0.4rem;
}
.nav__button__name {
    font-family: 'Gotham Pro Regular';
    font-size: 0.9rem;
    color: #fff;
    padding-left: 0.5rem;
    padding-right: 0.5rem;
}
.accountmenu__nav__items__icon {
    width: 20px;
    padding-right: 10px;
    align-items: center;
    filter: brightness(0) invert(1);
}
.accountmenu__content__navnew {
    position: absolute;
    display: flex;
    height: 70%;
    width: 16.5%;
    justify-content: left;
    align-items: left;
    background: var(--form-background);
    border: 0.5px solid rgba(45, 45, 45, 0.5);
    border-radius: 3px;
    flex-direction: column;
}
.accountmenu__content__navnew:focus {
    outline: 0;
}
.accountmenu__content__nav__items {
    display: flex;
    align-items: left;
    justify-content: left;
    flex-direction: column;
    width: 100%;
    height: 100%;
}

.accountmenu__content__nav__item_active {
    display: flex;
    justify-content: center;
    align-items: center;
}

.accountmenu__content__nav__item_unactive {
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: 100%;
}
.accountmenu__content__nav__item_unactive:hover {
    background-color: var(--hover-background);
}
.accountmenu__content__nav__item__text_active {
    font-family: 'Gotham Pro Regular';
    font-size: 1rem;
    color: #fff;
    box-sizing: border-box;
    background-color: var(--hover-background);
    width: 100%;
    padding-top: 5%;
    padding: 8% 8% 8% 12%;
}

.accountmenu__content__nav__item__text_unactive {
    font-family: 'Gotham Pro Regular';
    font-size: 1rem;
    color: #fff;
    box-sizing: border-box;
    width: 100%;
    padding: 8% 8% 8% 12%;
}

.content__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
}